import Vue from "vue";
import App from "./App.vue";
import router from "./router/router.js";

Vue.config.productionTip = false;

new Vue({
  router,
  render: h => h(App),
  watch: {
    $route(to) {
      if (to.name == "Index") {
        this.$router.go();
      }
    }
  }
}).$mount("#app");
